import { useState } from "react";
import Grid from "@mui/material/Grid";
import { PlusOutlined } from "@ant-design/icons";
import {
  Row,
  Col,
  Card,
  Button,
  List,
  Descriptions,
  Avatar,
  Radio,
  Switch,
  Upload,
  message,
  Modal,
  Form,
  Input,
  InputNumber,
} from "antd";
const { Meta } = Card;

function Feed() {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isUpdateModalVisible, setIsUpdateModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };
  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const showUpdateModal = () => {
    setIsUpdateModalVisible(true);
  };
  const handleUpdateCancel = () => {
    setIsUpdateModalVisible(false);
  };
  const layout = {
    labelCol: {
      span: 6,
    },
    wrapperCol: {
      span: 16,
    },
  };
  return (
    <>
      <Grid container direction="column" spacing={4}>
        <Grid item>
          <Grid item container justifyContent="flex-end">
            <Grid item>
              <Button
                type="dashed"
                className="ant-full-box"
                icon={<PlusOutlined />}
                onClick={showModal}
              >
                <span className="click">Add Feed</span>
              </Button>
              <Modal
                title="Add New Feed"
                visible={isModalVisible}
                onCancel={handleCancel}
              >
                <Form {...layout}>
                  <Form.Item
                    name={["user", "name"]}
                    label="Title"
                    rules={[
                      {
                        required: true,
                      },
                    ]}
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item name={["user", "mobileNo"]} label="Tag">
                    <Input />
                  </Form.Item>
                  <Form.Item
                    name={["user", "email"]}
                    label="Image"
                    // rules={[
                    //   {
                    //     type: "file",
                    //   },
                    // ]}
                  >
                    <Input type="file" />
                  </Form.Item>
                </Form>
              </Modal>
            </Grid>
          </Grid>
        </Grid>

        <Grid item>
          <Grid container spacing={3} justifyContent="center">
            <Grid item>
              <Card
                hoverable
                style={{
                  minWidth: 350,
                }}
                cover={
                  <img
                    height="300px"
                    alt="example"
                    src="data:image/png;base64,"
                  />
                }
              >
                <Meta title="Feed" description="Feed Description add here" />
                <Grid container justifyContent="flex-end" spacing={1}>
                  <Grid item>
                    <Button onClick={showUpdateModal}>Update</Button>
                    <Modal
                      title="Update Feed"
                      visible={isUpdateModalVisible}
                      onCancel={handleUpdateCancel}
                    >
                      <Form {...layout}>
                        <Form.Item
                          name={["user", "name"]}
                          label="Title"
                          rules={[
                            {
                              required: true,
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                        <Form.Item name={["user", "mobileNo"]} label="Tag">
                          <Input />
                        </Form.Item>
                        <Form.Item
                          name={["user", "email"]}
                          label="Image"
                          rules={[
                            {
                              type: "file",
                            },
                          ]}
                        >
                          <Input type="file" />
                        </Form.Item>
                      </Form>
                    </Modal>
                  </Grid>
                  <Grid item>
                    <Button>Delete</Button>
                  </Grid>
                </Grid>
              </Card>
            </Grid>
            <Grid item>
              <Card
                hoverable
                style={{
                  minWidth: 350,
                }}
                cover={
                  <img
                    height="300px"
                    alt="example"
                    src="data:image/png;base64,"
                  />
                }
              >
                <Meta title="Feed" description="Feed Description add here" />
                <Grid container justifyContent="flex-end" spacing={1}>
                  <Grid item>
                    <Button>Update</Button>
                  </Grid>
                  <Grid item>
                    <Button>Delete</Button>
                  </Grid>
                </Grid>
              </Card>
            </Grid>
            <Grid item>
              <Card
                hoverable
                style={{
                  minWidth: 350,
                }}
                cover={
                  <img
                    height="300px"
                    alt="example"
                    src="data:image/png;base64,"
                  />
                }
              >
                <Meta title="Feed" description="Feed Description add here" />
                <Grid container justifyContent="flex-end" spacing={1}>
                  <Grid item>
                    <Button>Update</Button>
                  </Grid>
                  <Grid item>
                    <Button>Delete</Button>
                  </Grid>
                </Grid>
              </Card>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </>
  );
}

export default Feed;
